.tabs {
  border: 2px solid #79747e;
  display: flex;
  justify-content: center;
  width: 90%;
  margin: auto;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
}

.tabs button {
  padding: 8px;
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
  width: 240px;
  border-right: 2px solid #79747e;
  color: #1d1b20;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.1px;
}

.tabs button:first-child {
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
}

.tabs button:last-child {
  border-right: none;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
}

.tabs button.active {
  background-color: #edf6ff;
}

.customer-content {
  justify-content: center;
  width: 80%;
  margin: auto;
  margin-top: 30px;
}

.device-add-btn {
  justify-content: normal;
  display: flex;
  align-items: end;
  margin-bottom: 10px;
}

.additional-info-title {
  color: #383838;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  margin-bottom: 30px;
}

.appointment-details-title {
  color: #171c1b;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin-bottom: 30px;
}

.appointment-details-container {
  text-align: center;
}

.initial-name {
  color: #989898;
  height: 40px;
  width: 40px;
  align-items: center;
  display: flex;
  justify-content: center;
  background: #d9d9d9;
  border-radius: 50%;
}

.customer-info {
  color: #171c1b;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.customer-name {
  color: #171c1b;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 33.6px;
}

.customer-infos {
  margin-bottom: 20px;
}

.customer-infos-title {
  color: #171c1b;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 33.6px;
}

.customer-infos-value {
  color: #1c1c1c;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.initial-name-container {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.appointment-duration-title,
.appointment-address-title {
  color: #171c1b;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 20.444px;
  margin-bottom: 10px;
}

.appointment-duration-sub-title,
.appointment-address-sub-title {
  color: #000;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.appointment-address {
  margin: 50px 0px;
}

.view-billing-btn {
  color: #1c1c1c;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.1px;
  padding: 10px 24px;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-2, 8px);
  flex: 1 0 0;
  border-radius: 100px;
  border: 1px solid #79747e;
  background: white;
  max-width: 182px;
  height: 40px;
  cursor: pointer;
}

.view-billing-action-btn {
  display: flex;
  justify-content: end;
  margin-top: 60px;
}

.remove-btn {
  color: #1c1c1c;
  line-height: 20px;
  letter-spacing: 0.1px;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-2, 8px);
  flex: 1 0;
  border-radius: 100px;
  border: 1px solid #79747e;
  background: white;
  max-width: 70px;
  height: 40px;
  cursor: pointer;
  margin-top: 25px;
}

.tab-action-btn {
  display: flex;
  justify-content: space-between;
  margin: 40px 20px;
  padding-bottom: 30px;
}

/* customer-details */
.customer-details-box {
  width: 100%;
  height: 320px;
  box-shadow: 0 2px 2px 1px rgb(0 0 0 / 0.1), 0 2px 4px 2px rgb(0 0 0 / 0.1);
}

.customer-flex {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

.four-width {
  flex: 4;
}

.six-width {
  flex: 6;
}

.eight-width {
  flex: 8;
}

.two-width {
  flex: 2;
}

.ten-width {
  flex: 10;
  overflow: hidden;
}

.twelve-width {
  flex: 12;
}

.customer-name {
  font-size: 20px;
  font-weight: 700;
  text-align: left;
}

.customer-small {
  font-size: 14px;
  font-weight: 400;
  text-align: left;
}

.customer-icon {
  width: 77px;
  height: 77px;
  line-height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #eeeeee;
  margin: auto;
  border-radius: 50%;
  font-weight: 500;
  font-size: 18px;
}

.customer-head {
  font-size: 16px;
  text-align: left;
}

.customer-icon-bg {
  width: 50px;
  height: 50px;
  line-height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #edf6ff;
  margin: auto;
  border-radius: 50%;
  font-weight: 500;
  font-size: 18px;
}

.customer-gray {
  color: #989898;
}

.customer-black {
  color: #1c1c1c;
}

.customer-table {
  font-size: 14px;
  font-weight: 400px;
  box-shadow: none !important;
}

.customer-table .MuiTableCell-head {
  font-size: 14px !important;
  font-weight: 400 !important;
  line-height: normal !important;
}

.customer-table .css-apqrd9-MuiTableBody-root,
.customer-table .css-1q1u3t4-MuiTableRow-root,
.customer-table .css-11xur9t-MuiPaper-root-MuiTableContainer-root {
  box-shadow: none !important;
}

.customer-table .css-1ex1afd-MuiTableCell-root {
  padding: 12px 12px 12px 0px !important;
  border: none !important;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: #1c1c1c !important;
}

.customer-table .css-1ygcj2i-MuiTableCell-root {
  padding: 12px 12px 12px 0px !important;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: #989898 !important;
  border: none !important;
}

/* customer-log */
.customer-log-head-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.customer-log-title {
  color: #171c1b;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.customer-log-table .css-1ex1afd-MuiTableCell-root {
  font-size: 14px !important;
  font-weight: 400 !important;
}

.f-bold-500 {
  font-weight: 500;
}

.error-msg {
  color: red;
}

.add-btn {
  color: white;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.1px;
  padding: 10px 24px;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-2, 8px);
  flex: 1 0 0;
  border-radius: 100px;
  border: 1px solid #79747e;
  background: #1c1c1c;
  max-width: 70px;
  height: 40px;
  cursor: pointer;
}


.success-msg-container {
  width: 45%;
  top: 68px;
  position: absolute;
  right: 10px;
}

.three-limit{
  overflow: hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;
}
