.clinic-timing-title {
    color: #383838;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;
}

.contact-no,
.from,
.to {
    color: #000;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: 20.444px;
}

.session-1-title,
.session-2-title {
    color: #383838;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 5px;
}

.time-div {
    margin: 0px !important;
}

.action-btn {
    display: flex;
    justify-content: end;
    margin-top: 60px;
}

.no-btn {
    color: #fff !important;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    /* padding: 10px 24px; */
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: #000 !important;
    /* max-width: 81px; */
    height: 40px;
    cursor: pointer;
}

