.chat-notification {
    display: flex;
    align-items: center;
    max-width: 24rem;
    margin: 0 auto;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background-color: #fff;
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
    width: 100%;
    height: 127px;
  }
  .chat-notification-logo-wrapper {
    flex-shrink: 0;
    background-color: #EDF6FF;
    border-radius: 50%;
    width: 77px;
    height: 77px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;


  }
  .chat-notification-logo {
    height: 3rem;
    width: 3rem;
  }
  .chat-notification-content {
    margin-left: 1.5rem;
  }
  .chat-notification-title {
    color: #1a202c;
    font-size: 1.6rem;
    line-height: 1.25;
  }
  .chat-notification-message {
    color: #718096;
    font-size: 0.80rem;
    line-height: 1.5;
  }

  /* Accesses */
  .access-box{
    width: 100%;
    height: 270px;
  }

  .flex-space-equally{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .component-title{
    font-size: 16px;
    font-weight: 400;
    line-height: 17px;
  }


  .box-center{
    margin: auto;
  }

  .btn-chart{
    border: solid 1px #999999;
    padding: 3px 3px;
    border-radius: 11px;
    text-wrap: nowrap;
    width: auto;
    font-size: 9px;
  }
  .btn-chart.active{
    border: solid 1px #1C1B1F;
    background: #1C1B1F;
    color: #fff;
  }

  .flex-start{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 3px;
    overflow-y: hidden;
    width: 100%;
    margin-top: 5px;
  }
.chart-infto-dot{
  width:8px;
  height:8px;
  border-radius: 50%;
  display: inline-block;
  margin-right: 2px;
}
.chart-infto-title{
  font-size: 11px;
 display: inline-block;
}
.chart-infto-num{
  font-size: 11px;
  display: inline-block;
}

.MuiBarElement-root{
  border-radius: 10px !important;
}


.ant-float-btn-body{
  background: url('../../assets/images/vizzhyAI.png') no-repeat !important;
  width: 100px !important;
  height: 100px !important;
  right: 50px;
}
.ant-float-btn{
  width: auto !important;
}

.ant-float-btn-body svg{
  display: none !important;

}