.personal-details-heading-title {
    color: #000;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    text-align: left;
}

.personal-details-container {
    text-align: center;
    margin: 10px;
}
.personal-details-head-box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    font-weight: 400;
}

.details-box{
    padding: 10px;
    width: 260px;
    box-shadow: 0 4px 6px 1px rgb(0 0 0 / 0.1), 0 2px 4px 2px rgb(0 0 0 / 0.1);
}
.details-box-flex{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
}

.p-icon{
    width: 50px;
    height: 50px;
    line-height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #EEEEEE;
    margin: auto;
    border-radius: 50%;
    font-weight: 500;
    font-size: 18px;
}
.p-Name{
    font-size: 20px;
}
.p-divider{
    border: 1px solid #EEEEEE
}
.font-w-500{
    font-weight: 500;
}
.p-lable{
    font-weight: 700;
    font-size: 16px;
}

.p-w-400{
    width: 380px;
    padding: 10px;
}

.action-btn {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}

.no-btn {
    color: #1C1C1C;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: white;
    max-width: 100px;
    height: 40px;
    cursor: pointer;
}

.yes-btn {
    color: white;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: #1C1C1C;
    max-width: 125px;
    height: 40px;
    cursor: pointer;
}

.img-center{
    text-align: center !important;
    margin: auto;
    width: 243px;
}

.input-reassign{
    height: 50px !important;
    width: 100%;
    padding: 5px !important;
}
