.user-status-heading-title {
    color: #000;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
}

.user-status-heading-sub-title {
    color: #6F6F6F;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-top: 16px;
}

.user-status-container {
    text-align: center;
    margin: 40px 30px;
}

.action-btn {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}

.no-btn {
    color: #1C1C1C;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: white;
    max-width: 100px;
    height: 40px;
    cursor: pointer;
}

.yes-btn {
    color: white;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: #1C1C1C;
    max-width: 100px;
    height: 40px;
    cursor: pointer;
}