.reassign-doctor-heading-title {
    color: #000;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    text-align: left;
}


.reassign-doctor-container {
    text-align: center;
    margin: 10px;
}

.action-btn {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}

.no-btn {
    color: #1C1C1C;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: white;
    max-width: 100px;
    height: 40px;
    cursor: pointer;
}

.yes-btn {
    color: white;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: #1C1C1C;
    max-width: 125px;
    height: 40px;
    cursor: pointer;
}

.img-center{
    text-align: center !important;
    margin: auto;
    width: 243px;
}

.input-reassign{
    height: 50px !important;
    width: 100%;
    padding: 5px !important;
}
