.MuiPaper-elevation4 {
    box-shadow: none;
    border-bottom: 1px solid #e0e0e0;
    /* margin-top: 2px; */
    background: white;
}

.header-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.heading-title {
    color: #1C1C1C;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
}

.heading-sub-title {
    color: #9B9B9B;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.username {
    color: #000;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
}

.role {
    color: #989898;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
}

.initial-name {
    color: #989898;
    height: 40px;
    width: 40px;
    align-items: center;
    display: flex;
    justify-content: center;
    background: #D9D9D9;
    border-radius: 50%;
}

.header-user-detail {
    margin: 0px 10px;
}

.MuiDrawer-paper {
    overflow: auto;
}

.logout-title-container {
    margin: 20px 0px 30px 30px;
    display: flex;
    align-items: center;
}

.logout-title {
    color: #A9A9A9;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-transform: capitalize;
    cursor: pointer;

}