.upload-box {
    background-color: #E7E7E799;
    border:1px dashed #000;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding:20px;
    flex-direction: column;
    margin-bottom: 20px;
}

.upload-bar {
    border:1px solid #E3E3E3;
    margin-bottom:10px;
    padding:8px 10px;
    font-size:13px;
    display: flex;
    justify-content: space-between;
    position: relative;
}

.upload-bar .line-bar {
    position: absolute;
    width:20%;
    left:0;
    bottom:-2px;
    height: 3px;
    border-radius: 50px;
    background-color: #000;
}

.upload-bar .line-bar.w-50 {
    width:50%
}

.upload-bar .line-bar.w-70 {
    width:70%
}



.cancel-btn{
    color: #1C1C1C;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.1px;
    padding: 10px 24px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-2, 8px);
    flex: 1 0 0;
    border-radius: 100px;
    border: 1px solid #79747E;
    background: white;
    max-width: auto;
    height: 40px;
    cursor: pointer;
    flex:0;
}

.action-btn {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}


.upload-btn {
    color: white;
    text-align: center;
