.menu-container {
    display: flex;
    justify-content: space-between;
    margin: 0px 30px;
    padding-top: 40px;
}

.menu-title-container {
    display: flex;
    align-items: center;
    cursor: pointer;
}
.sub-menu-container{
    display: flex;
    justify-content: space-between;
    margin: 0px 30px;
    padding-top: 40px;
}

.menu-icon {
    margin-right: 20px;
}
.logout-menu-icon{
    margin-right: 15px;
}

.menu-title {
    color: #A9A9A9;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-transform: capitalize;
}
.menu-title-active {
    color: #1C1C1C;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-transform: capitalize;
}

